div.app {
  height: 100vh;
  width: 100vw;
  background-image: url(data:image/png;base64,);
  background-color: var(--bgc);
  background-size: 7%;
  position: relative;
  /* overflow: hidden; */
  /* overflow potem dać */
}

main {
  position: absolute;
  top: 10%;
  bottom: 10%;
  left: 5%;
  right: 10%;
  /* background-color: darkred; */
  /* border: 2px solid black; */
}

nav {
  position: absolute;
  top: 50%;
  right: 0;
  /* left moze tak, moze nie. zobaczymy */
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

/* MEDIA QUERIES */

@media (max-width: 1023px) {
  nav {
    top: auto;
    bottom: 0;
    left: 50%;
    right: auto;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}
:root {
  --ratio: 1;
  /* 0.8 means 80% */

  /* --bgc: #272727; */
  --bgc: #070730;
  --h1: #EDEDED;
  --h2: #0086FF;
  --shadow: #3D3D99;
  --red: #E32B10;
}
nav {
  background-color: rgba(0, 0, 0, 0.2);
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.2);
}

i[class^="icon-"]:before,
i[class*=" icon-"]:before {
  margin: 0;
}

/* delete margin which fontello gives */

nav ul {
  list-style: none;
}

nav ul li i {
  display: block;
  color: var(--h1);
  font-size: 30px;
  margin: 15px 10px 15px 0;
  padding: 10px;
  -webkit-transition: .3s;
  transition: .3s;
  -webkit-text-stroke: 1px var(--bgc);
  /* makes icons thinner */
}

nav ul li a.active i {
  color: var(--h2);
}

nav ul li:nth-of-type(1),
nav ul li:nth-of-type(2),
nav ul li:nth-of-type(3) {
  /* border-bottom: 1px solid #4E4E50;  */
}

nav ul li i:hover {
  color: var(--shadow);
}



/* MEDIA QUERIES */

@media (max-width: 1023px) {
  nav ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 100vw;
  }

  nav ul li {
    -webkit-flex-basis: 25%;
            flex-basis: 25%;
  }

  nav ul li i {
    text-align: center;
    margin: 0;
  }

  nav ul li:nth-of-type(1),
  nav ul li:nth-of-type(2),
  nav ul li:nth-of-type(3) {
    border-right: 2px solid var(--h1);
  }
}

@media (max-width: 359px) {
  nav ul li i {
    font-size: 20px;
  }
}
