div.projects {
  animation: makeVisible 0.5s linear both;
  height: calc(95% - 40px);
  overflow: hidden;
}

div.projects h1 {
  color: var(--h1);
  font-family: "Josefin Sans", sans-serif;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase;
  margin: 20px 0 40px 20px;
  text-shadow: -1px 0 4px var(--bgc), 0 1px 4px var(--bgc), 1px 0 4px var(--bgc),
    0 -1px 4px var(--bgc), 4px 4px 3px var(--shadow), 6px 6px 6px var(--h2);
}

div.projects .container {
  display: flex;
  /* flex-wrap: wrap; */
  background-color: var(--h1);
  border-radius: 5px;
  box-shadow: 0 0 8px black;
  width: 80%;
  height: 80%;
  margin: 0 auto;
  transform-style: preserve-3d;
  transition: 0s;

  /* animation: makeVisible 0.5s linear both; */
  /* animation: shift 3s ease-in-out both infinite; */
}

div.projects .container.shift {
  /* animation: shift 3s ease-in-out both; */
  transform: rotateY(-360deg);
  transition: 0.9s cubic-bezier(0, 0, 0, 0.52);
}
div.projects .container.shift-left {
  /* animation: shift 3s ease-in-out both; */
  transform: rotateY(360deg);
  transition: 0.9s cubic-bezier(0, 0, 0, 0.52);
}

div.projects .container.shift a {
  /* transform: rotateY(-90deg); */
  /* transition: .6s; */
  /* opacity: 0; */
}

div.projects .container.shift .content {
}

div.projects .container a.photo-container {
  display: block;
  width: 70%;
  height: 100%;
  /* box-shadow: 0 0 8px black; */
}

div.projects .container img {
  box-shadow: 0 0 8px black;
  object-fit: cover;
  object-position: 50% 0%;
  display: block;
  height: 100%;
  width: 100%;
}

div.projects .content {
  width: 50%;
  background-color: var(--h1);
  box-shadow: 0 0 20px black inset;
}

div.projects .content h2 {
  font-size: 30px;
  font-family: "Josefin Sans", sans-serif;
  color: var(--h2);
  text-align: center;
  margin: 20px 0 5px;
  text-shadow: 2px 2px 3px var(--shadow);
}

div.projects .content a {
  display: block;
  color: #666;
  text-decoration: none;
  font-size: 10px;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  /* padding: 10px; */
}

div.projects .content a span {
  color: var(--shadow);
  font-weight: bold;
}

div.projects .content p {
  font-family: "Josefin Sans", sans-serif;
  color: #222;
  text-align: center;
  padding: 20px 10px;
  line-height: 20px;
  /* margin-top: 15px; */
  /* margin-left: 10%; */
}

/* arrows: */
div.projects .wrapper-for-arrows {
  position: relative;
  height: 80%;
  perspective: 800;
}

div.projects .wrapper-for-arrows i.fas {
  position: absolute;
  top: 50%;
  left: 0;
  /* transform: translate(-50%, -50%); */
  transform: translateY(-50%);
  color: var(--h1);
  font-size: 60px;
  /* text-shadow: 0 0 4px black, 2px 2px 6px var(--h2); */
  text-shadow: 1px 1px 6px var(--h2), -1px 1px 6px var(--h2),
    1px -1px 6px var(--h2), -1px -1px 6px var(--h2);
  cursor: pointer;
}

div.projects .wrapper-for-arrows i.fas:nth-of-type(2) {
  left: auto;
  right: 0;
}

@keyframes shift {
  0% {
    transform: translateX(300%);
    opacity: 0;
  }

  10% {
    transform: translateX(0);
  }

  15% {
    opacity: 1;
  }

  85% {
    opacity: 1;
  }

  90% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-300%);
    opacity: 0;
  }
}
